#root {
  width: 100%;
  height: 100%;
}

.container {
  position: fixed;
  @extend %center;
  max-width: 960px;
  width: calc(100% - 2rem);
  height: 640px;
  padding: 2rem 0;
  background-color: $gray-color-1;
  box-sizing: border-box;
  box-shadow: 0 0 10px 5px rgba($base-color, 0.3);

  @media #{$breakpoint-mobile} {
    height: 480px;
  }
}
