    height: 3rem; }
  .button.secondary {
    width: 7rem;
    height: 3rem;
    background-color: #ffffff;
    border: 1px solid #74757b;
    color: #74757b; }

.form {
  width: 100%;
  display: flex;
  justify-content: space-between; }

.textarea {
  width: 100%;
  font-weight: 400; }

.checkbox {
  position: absolute;
  display: none; }

.checkbox + label {
  position: relative;
  display: inline-block;
  cursor: pointer;
  width: 18px;
  height: 18px; }
  .checkbox + label:hover:before {
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-name: hover-color;
            animation-name: hover-color; }
  .checkbox + label:before {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    width: 100%;
    height: 100%;
    content: "";
    border: 1px solid #f86457; }
  .checkbox + label:after {
    position: absolute;
    display: none;
    content: ""; }

.checkbox:checked + label:before {
  -webkit-animation-name: none;
          animation-name: none; }

.checkbox:checked + label:after {
  display: block; }

.checkbox + label:before {
  border-radius: 5px; }

.checkbox + label:after {
  top: 2px;
  left: 6px;
  box-sizing: border-box;
  width: 6px;
  height: 12px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  border-width: 2px;
  border-style: solid;
  border-color: #ffffff;
  border-top: 0;
  border-left: 0; }

.checkbox:checked + label:before {
  border: #f86457;
  background: #f86457; }

.todo-filter {
